.card {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  padding: 32px 16px 16px;
  display: flex;
}

.card__preview {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__preview--model {
  background-image: url("imac.202c52ff.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.card__title {
  text-align: left;
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  text-align: left;
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: block;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  text-align: right;
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-content {
  text-align: left;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  margin: 0 auto;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  background: #000c;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

h2, p {
  margin: 0;
}

.products .products__card {
  width: 200px;
  height: 408px;
}
